:root {
  /* fallback seulement: les vraies valeurs viennent de settings.cssVars() */
  --px-per-min: 1.2;
  --hours-col-w: 72px;
  --day-min-w: 220px;
  --col-gap: 8px;
  --col-pad: 6px;
  --day-head-h: 36px;
  --slot-hover-min-h: 128px;
  --month-cell-h: 120px;   /* hauteur par cellule (ligne) */
  --month-cell-pad: 6px;   /* padding interne cellule */
}

/* Viewport scrollable (vertical + horizontal) */
.events-grid-scroll {
  max-height: calc(100vh - 220px);
  overflow: auto;
  overscroll-behavior: contain;
}

/* Grille 7 jours */
.events-grid {
  display: grid;
  grid-template-columns: var(--hours-col-w) repeat(7, minmax(var(--day-min-w), 1fr));
  gap: var(--col-gap);
  min-width: calc(var(--hours-col-w) + 7 * var(--day-min-w) + 7 * var(--col-gap));
}

/* Colonne heures sticky à gauche */
.events-hours-col {
  background: var(--panel);
  border: 1px solid var(--line);
  border-radius: 8px;
  padding: 8px 0;
  padding-top: var(--day-head-h); /* aligne les traits avec les cases */
  position: sticky;
  left: 0;
  z-index: 3;
}
.events-hour-label {
  height: calc(60 * var(--px-per-min) * 1px);
  font-size: 12px;
  color: var(--muted);
  padding-left: 10px;
  box-sizing: border-box;
  position: relative;
}
.events-hour-label::after {
  content: '';
  position: absolute;
  right: 8px;
  left: 8px;
  bottom: 0;
  height: 1px;
  background: var(--line);
  opacity: 0.6;
}

/* Colonne jour */
.events-day-col {
  position: relative;
  height: calc(660 * var(--px-per-min) * 1px); /* 08:00 → 19:00 */
  border: 1px solid var(--line);
  border-radius: 8px;
  background-color: var(--panel);
  overflow: visible; /* pour le zoom au survol */
}
.events-day-header {
  height: var(--day-head-h);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text);
  font-weight: 600;
  border-bottom: 1px solid var(--line);
}
.events-day-content {
  position: relative;
  height: calc(100% - var(--day-head-h));
  padding: 0 var(--col-pad);
  overflow: visible;
  background:
    repeating-linear-gradient(
      to bottom,
      transparent 0,
      transparent calc(60 * var(--px-per-min) * 1px - 1px),
      var(--line) calc(60 * var(--px-per-min) * 1px - 1px),
      var(--line) calc(60 * var(--px-per-min) * 1px)
    );
}

/* Ligne "maintenant" (optionnelle si tu veux l'activer) */
.events-now-line {
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background: #ef4444;
  opacity: 0.9;
  pointer-events: none;
}

/* Case événement */
.events-slot {
  box-sizing: border-box;
  position: absolute;
  top: var(--slot-top);
  height: var(--slot-height);
  left: var(--slot-left);
  width: var(--slot-width);

  padding: 8px 10px;
  border-radius: 8px;
  color: var(--text);
  box-shadow: var(--shadow);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 4px;
  border: 1px solid var(--line);

  transition: transform 120ms ease, box-shadow 120ms ease, left 80ms ease, width 80ms ease, height 120ms ease;
  transform-origin: center center;
}
.events-slot:hover,
.events-slot:focus-within {
  transform: scale(1.02);
  z-index: 9999;
  box-shadow: 0 6px 18px rgba(0,0,0,0.25), 0 2px 8px rgba(0,0,0,0.2);
  left: var(--col-pad);
  width: calc(100% - (var(--col-pad) * 2));
  height: max(var(--slot-height), var(--slot-hover-min-h));
  overflow: visible;
}

.events-slot-title {
  font-weight: 800;
}
.events-slot-time {
  font-size: 12px;
  opacity: 0.9;
}
.events-slot-loc {
  font-size: 12px;
  opacity: 0.8;
}

/* Scroll viewport pour le mois */
.events-month-scroll {
  max-height: calc(100vh - 220px);
  overflow: auto;
}

/* Grille mensuelle: 7 colonnes, 5-6 lignes */
.events-month-grid {
  display: grid;
  grid-template-columns: repeat(7, minmax(var(--day-min-w), 1fr));
  gap: var(--col-gap);
  min-width: calc(7 * var(--day-min-w) + 7 * var(--col-gap));
}

/* Entête des jours de la semaine */
.events-month-weekdays {
  display: contents;
}
.events-month-weekday {
  text-align: center;
  font-weight: 600;
  color: var(--muted);
  padding: 6px 0;
}

/* Cellule jour */
.events-month-cell {
  border: 1px solid var(--line);
  border-radius: 8px;
  background: var(--panel);
  min-height: var(--month-cell-h);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.events-month-cell.is-other-month {
  opacity: 0.7;
}
.events-month-cell-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4px 8px;
  border-bottom: 1px solid var(--line);
  color: var(--text);
  font-weight: 700;
  height: 28px;
}
.events-month-daynum {
  border-radius: 999px;
  padding: 0 6px;
}
.events-month-cell.is-today .events-month-daynum {
  background: #2563eb;
  color: white;
}

.events-month-items {
  padding: var(--month-cell-pad);
  display: flex;
  flex-direction: column;
  gap: 4px;
  overflow: hidden; /* masque le “trop” vertical */
}

/* Item événement compact dans une cellule */
.events-month-item {
  display: flex;
  align-items: center;
  gap: 6px;
  min-height: 20px;
  font-size: 12px;
  line-height: 1.3;
  color: var(--text);
  background: color-mix(in srgb, var(--panel) 70%, transparent);
  border-radius: 6px;
  padding: 3px 6px;
  border: 1px solid var(--line);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.events-month-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  flex: 0 0 auto;
}
.events-month-more {
  font-size: 12px;
  color: var(--muted);
}